body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  background-image: url(/static/media/kembawalker-fivea-bgkd.cf6290f9.jpg);
  height: 1000px;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
header {
  padding: 12px;
}

@-webkit-keyframes TitleSqueeze {
  40% {-webkit-transform: scale(1.5, 0.8);transform: scale(1.5, 0.8);}
  70% {-webkit-transform: scale(0.8, 1.2);transform: scale(0.8, 1.2);}
  85% {-webkit-transform: scale(1.1, 0.9);transform: scale(1.1, 0.9);}
}

@keyframes TitleSqueeze {
  40% {-webkit-transform: scale(1.5, 0.8);transform: scale(1.5, 0.8);}
  70% {-webkit-transform: scale(0.8, 1.2);transform: scale(0.8, 1.2);}
  85% {-webkit-transform: scale(1.1, 0.9);transform: scale(1.1, 0.9);}
}

@-webkit-keyframes TitleShrink {
  0% {font-size: 500px;}
}

@keyframes TitleShrink {
  0% {font-size: 500px;}
}

@-webkit-keyframes TitleRotate {
  0% {-webkit-transform: rotate(320deg);transform: rotate(320deg);}
  85% {-webkit-transform: rotate(270deg);transform: rotate(270deg);}
  97% {-webkit-transform: rotate(30deg);transform: rotate(30deg);}
}

@keyframes TitleRotate {
  0% {-webkit-transform: rotate(320deg);transform: rotate(320deg);}
  85% {-webkit-transform: rotate(270deg);transform: rotate(270deg);}
  97% {-webkit-transform: rotate(30deg);transform: rotate(30deg);}
}

@-webkit-keyframes FadeIn {
  0%   { opacity: 0; }
  92%  { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes FadeIn {
  0%   { opacity: 0; }
  92%  { opacity: 0; }
  100% { opacity: 1; }
}

a {
  color: #00c0ce;
}

.title {
  position: relative;
  padding: 4px;
  font-size: 56px;
  font-style: oblique;
  text-align: center;
  display: inline-block;
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
  -webkit-animation: TitleSqueeze 1s ease 6.8s, TitleShrink 6.4s ease-in;
          animation: TitleSqueeze 1s ease 6.8s, TitleShrink 6.4s ease-in;
}

.bordered {
  border: 5px solid;
  border-radius: 8px;
  padding: 4px;
  width: 48px;
  display: inline-block;
  -webkit-transform: skew(10deg);
          transform: skew(10deg);
}

.link {
  font-weight: bold;
  font-size: 14px;
  display: inline-flex;
  position: absolute;
  top: 32px;
  right: -32px;
  align-items: flex-end;
  -webkit-animation: 10s FadeIn;
          animation: 10s FadeIn;
}

.link span {
  margin-right: 20px;
}

